/**
 *  
 * @param {*} authOb
 */
yh.prototype.setAuthInfo = function(authOb){
    try {
        yh.prototype.userToken = authOb.userToken; // 

        if (authOb.bizDomain === null || authOb.bizDomain === "" || authOb.bizDomain === "null") {
          console.log("ID,ID");
          if (yh.prototype.cdn === 2 || yh.prototype.cdn === "2") {
             yh.prototype.bizDomain = "1001"
          } else {
            yh.prototype.bizDomain = "1000"
          }
        }else{
            console.log("IDCDN");
            if (yh.prototype.cdn === 2 || yh.prototype.cdn === "2") {
                yh.prototype.bizDomain = "1001"
            } else {
                yh.prototype.bizDomain = "1000"
            }
            // yh.prototype.bizDomain = authOb.bizDomain; // yh.prototype.bizDomain = authOb.bizDomain; // 
        }

        yh.prototype.userGroup = authOb.userGroup; // 

        yh.prototype.productIds = authOb.productIds; // ID

    if (authOb.areaCode !== "" && authOb.areaCode !== null && authOb.areaCode||undefined) {
        yh.prototype.areaCode = authOb.areaCode  // 
    }
        yh.prototype.userName = authOb.userName;  // 

        // yh.prototype.stbType = authOb.stbType;

        yh.prototype.telephone = authOb.telephone;  // 

        yh.prototype.epgDomain = authOb.epgDomain; // epg

        yh.prototype.aaaDomain = authOb.aaaDomain;  // aaa

        yh.prototype.ucenterDomain = authOb.ucenterDomain; // 

        yh.prototype.logDomain = authOb.logDomain; // 

        yh.prototype.updateServer = authOb.updateServer; // 
    } catch (error) {
        console.log("");
    }
}

