let baseInfo = { userId: yh.userId, mac: yh.mac, createTime: c.formatDate(new Date().getTime()), appKey: '', operatorId: yh.operator_id, terraceId: '', contentId: '', brand: yh.brand, mode: yh.mode, apkVersion: yh.apk_version, reserveGroup: yh.reserve_group, collectType:'BS'};
let asset = { ChannelID: '', ChannelName: '', ChannelStatus: '', PagePath: '', SrcPagePath: '', PageName:''}
let commonFields = function(type){
    let xhaObject  = {};
    xhaObject.Event_Type = type;
    xhaObject.UserID = yh.userId;
    xhaObject.Mac = yh.mac;
    xhaObject.CreateTime = baseInfo.createTime;
    xhaObject.AppKey = baseInfo.appKey;
    return xhaObject;
    // return field + "|" + baseInfo.userId + "|" + baseInfo.mac + "|" + baseInfo.createTime + "|" + baseInfo.appKey+"|";
}
// 
xha.init = function(){
    let xhaObj = commonFields('01');
    xhaObj.OperatorID = yh.operator_id;
    xhaObj.TerraceID = yh.terrace_id;
    xhaObj.ContentID = yh.ContentID;
    xhaObj.Brand = yh.brand;
    xhaObj.Mode = yh.mode;
    xhaObj.ApkVersion = yh.apk_version_name;
    xhaObj.ReserveGroup = '0' + yh.areaCode; // 20190124 30,4
    xhaObj.CollectType = collectType(yh.stbType);
    xhaObj.Reserve1 = "";
    xhaObj.Reserve1 = "";
    console.log("", JSON.stringify(xhaObj));
    groupArg("01", xhaObj);
}

// 
xha.collect = function(cId,cName,cStatus){
    let xhaObj = commonFields('08');
    xhaObj.ChannelID = cId;
    xhaObj.ChannelName = cName;
    xhaObj.ChannelStatus = cStatus;
    xhaObj.Reserve1 = "";
    xhaObj.Reserve2 = "";
    xhaObj.OperatorID = yh.operator_id;
    console.log("", xhaObj);
    groupArg("08", xhaObj);
}

// 
xha.pageLoad = function (PagePath,SrcPagePath, PageName, AsssetID, Id, TYPE){
    // let value = commonFields('06') + PagePath + "|" + SrcPagePath + "|" + encodeURIComponent(PageName) + "|" + AsssetID + "|" + Id + "|" + TYPE + "|" + Reserve1 + "|" + baseInfo.operatorId;
    let xhaObj = commonFields('06');
    xhaObj.PagePath = PagePath;
    xhaObj.SrcPagePath = SrcPagePath;
    xhaObj.PageName = PageName;
    xhaObj.AssetId = AsssetID; // ID
    xhaObj.Id = Id;// ID
    xhaObj.TypeId = TYPE; // 
    xhaObj.OperatorID = yh.operator_id;
    console.log("", JSON.stringify(xhaObj));
    groupArg("06", xhaObj);
}

//  
xha.pay = function (ProductType, ProductID, ProductName, EndTime, Action, OrderStatus, Fail, OrderUrl, Confirmation, UrlName, ProviderID) {
    let xhaObj = commonFields('07');
    xhaObj.ProductType = ProductType;
    xhaObj.ProductID = ProductID;
    xhaObj.ProductName = ProductName;
    xhaObj.ProviderID = ProviderID;
    xhaObj.EndTime = EndTime;
    xhaObj.Action = Action;
    xhaObj.OrderStatus = OrderStatus;
    xhaObj.Fail = Fail;
    xhaObj.OrderUrl = OrderUrl;
    xhaObj.Confirmation = Confirmation;
    xhaObj.UrlName = UrlName;
    xhaObj.Reserve1 = "";
    xhaObj.Reserve2 = "";
    xhaObj.OperatorID = yh.operator_id;
    console.log("", xhaObj);
    groupArg("07", xhaObj);
}

// 
xha.ad = function (AdID, AdName, AdlStatus){
    let xhaObj = commonFields('11');
    xhaObj.AdID = AdID;
    xhaObj.AdName = AdName;
    xhaObj.AdlStatus = AdlStatus;
    xhaObj.Reserve1 = "",
    xhaObj.Reserve2 = "",
    xhaObj.OperatorID = yh.operator_id;
     console.log("", xhaObj);
     groupArg("11", xhaObj);
}

xha.heart =  function(type){
    let xhaObj = commonFields('200');
    xhaObj.Switch = type;
    console.log(":"+type);
    groupArg("200", xhaObj);
}

xha.app = function(appName,status,enterType){
    let xhaObj = commonFields('10');
    xhaObj.ApplylID = "";
    xhaObj.ApplylName = appName
    xhaObj.ApplyColumnID = "";
    xhaObj.ApplyColumnName = "";
    xhaObj.ApplylStatus = status
    xhaObj.EnterType = enterType
    xhaObj.ApplyType = "1";
    xhaObj.Reserve1 = "";
    xhaObj.Reserve2 = "";
    xhaObj.OperatorID = yh.operator_id
    console.log("", xhaObj);
    groupArg("10", xhaObj);
}

function collectType(stbType) {
    if (stbType === "0" || stbType === 0){  // 
        return 0;
    } else if (stbType === "1" || stbType === 1) { // 
        return  1;
    }else{
        return 1;
    }
}

// window.xhaCallback = function (params) {
